#ui-left,
#ui-right,
#ui-left-column-2 {
    & .combat-dock {
        align-self: center;
    }
}

#ui-left-column-2:has(.combat-dock) {
    position: relative;
    .combat-dock {
        margin-left: 0;
        padding-left: 0;
        margin-top: calc((var(--control-size) * 1.1) / var(--ui-scale));
    }
}

#combat-dock {
    max-width: 100%;
    width: auto !important;
    height: auto !important;
    overflow-x: visible;
    overflow-y: visible;
    pointer-events: none;
    background: transparent;
    border: none;
    box-shadow: none;
    .window-content {
        backdrop-filter: none;
        padding: 0;
    }

    &:hover {
        .window-header {
            opacity: 0.3;
            &:hover {
                opacity: 1;
            }
        }
    }

    & .window-header {
        opacity: 0;
        border: 1px solid;
        transition: all 0.2s ease-in-out;
        max-width: fit-content;
        aspect-ratio: 1;
        pointer-events: all;
    }

    &:has(.window-content):not(:has(.reverse-header-position)) {
        height: var(--carousel-floating-size) !important;
        min-height: var(--carousel-floating-size);
    }

    &:has(.reverse-header-position) {
        flex-direction: row;
        max-width: var(--carousel-floating-size);
        min-width: var(--carousel-floating-size);
        & .window-header {
            max-height: fit-content;
        }
    }
}

.combat-dock {
    display: flex !important;
    justify-content: var(--carousel-alignment);
    pointer-events: none;
    width: auto !important;
    max-height: 100%;
    overflow-x: visible;
    overflow-y: visible;

    &.hidden {
        display: none !important;
    }

    & #combatants::-webkit-scrollbar {
        transition: opacity 0.2s ease-in-out;
    }

    & #combatants {
        pointer-events: none;
        display: flex;
        flex-direction: var(--carousel-direction);
        align-items: var(--carousel-align-items);
        padding: 0 0.5rem;
        max-width: 100%;
        width: 100%;
        min-height: calc(
            var(--combatant-portrait-size) * var(--combatant-portrait-aspect) *
                1.2 * 1.2
        );
        overflow: var(--carousel-overflow);
    }

    & .separator {
        opacity: 0.7;
        display: flex;
        align-items: center;
        gap: 0.4rem;

        .round {
            color: white;
            font-size: 1rem;
            text-shadow: 0 0 5px black;
            display: flex;
            align-items: center;
        }

        .line {
            border: 1px solid white;
        }

        &.horizontal {
            height: 100%;
            max-height: calc(
                var(--combatant-portrait-size) *
                    var(--combatant-portrait-aspect) * 1.2
            );
            .line {
                height: 100%;
                border-bottom-right-radius: 50%;
                border-bottom-left-radius: 50%;
            }
        }
        &.vertical {
            width: 100%;
            flex-direction: column;
            max-width: calc(var(--combatant-portrait-size) * 1.2);
            .line {
                width: 100%;
                border-radius: 50%;
                margin: 0.1rem 0;
            }
        }
    }

    &:not(:hover) {
        & #combatants {
            &::-webkit-scrollbar-thumb {
                background-color: transparent;
                border-color: transparent;
            }
            &::-webkit-scrollbar-track {
                display: none;
            }
        }
    }

    &:hover {
        & .buttons-container {
            opacity: 1;
        }
    }

    & .buttons-container {
        pointer-events: all;
        transition: opacity 0.2s ease-in-out;
        opacity: 0;
        color: var(--color-text-light-0);
        display: flex;
        flex-direction: column;
        padding: 1rem 0;
        justify-content: flex-start;
        gap: 0.3rem;
        align-items: center;

        i {
            color: var(--color-text-light-3);
            padding: 0.5rem;
            text-shadow: 0 0 5px var(--color-text-dark-1);

            border: 2px inset var(--carousel-tooltip-color);
            border-radius: 50%;
            background: var(--carousel-tooltip-bg-color);
            box-shadow:
                0px 0px 10px 0px rgba(0, 0, 0, 0.873),
                inset 0px 0px 10px 0px rgba(0, 0, 0, 0.873);
            width: 1.5rem;
            height: 1.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: small;

            &:hover {
                filter: brightness(1.2);
                cursor: pointer;
            }
        }
    }
}
